"use client";

import React, { useState } from "react";
import { notFound } from "next/navigation";
import { useParams } from "next/navigation";
import PremiumCalculatorForm from "@/components/PremiumCalculatorForm";
import { getInsuranceData } from "@/lib/insuranceData";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import Link from "next/link";
import { Badge } from "@/components/ui/badge";

export default function CalculatorPage() {
  const params = useParams();
  const type = params.type as string;
  
  const data = getInsuranceData(type);
  
  if (!data) {
    notFound();
  }

  const [calculatedPremium, setCalculatedPremium] = useState<number | null>(null);
  const [formData, setFormData] = useState<any>(null);

  const insuranceTypeInfo = {
    auto: {
      title: "Asuransi Kendaraan",
      description: "Hitung premi asuransi kendaraan Anda berdasarkan kendaraan dan riwayat berkendara.",
      icon: "🚗"
    },
    health: {
      title: "Asuransi Kesehatan",
      description: "Dapatkan penawaran asuransi kesehatan yang dipersonalisasi berdasarkan profil kesehatan Anda.",
      icon: "🏥"
    },
    life: {
      title: "Asuransi Jiwa",
      description: "Hitung premi asuransi jiwa berdasarkan usia, kesehatan, dan kebutuhan pertanggungan Anda.",
      icon: "🛡️"
    },
    home: {
      title: "Asuransi Rumah",
      description: "Dapatkan penawaran asuransi rumah yang akurat berdasarkan detail properti Anda.",
      icon: "🏠"
    }
  };

  const typeInfo = insuranceTypeInfo[type as keyof typeof insuranceTypeInfo];
  if (!typeInfo) {
    notFound();
  }

  const handleCalculate = (premium: number, formData: any) => {
    setCalculatedPremium(premium);
    setFormData(formData);
  };

  const getAdjustedPrice = (originalPrice: string, premium: number) => {
    const basePrice = parseInt(originalPrice.replace(/[^0-9]/g, ''));
    const adjustment = (premium / 100) * 0.8; // 80% dari premi yang dihitung
    return Math.round(basePrice + adjustment);
  };

  const formatRupiah = (amount: number) => {
    return new Intl.NumberFormat('id-ID', {
      style: 'currency',
      currency: 'IDR',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0
    }).format(amount);
  };

  return (
    <div className="space-y-8">
      {/* Header */}
      <div className="text-center space-y-4">
        <div className="text-6xl mb-4">{typeInfo.icon}</div>
        <h1 className="text-4xl font-bold">Kalkulator Premi {typeInfo.title}</h1>
        <p className="text-lg text-muted-foreground max-w-3xl mx-auto">
          {typeInfo.description}
        </p>
      </div>

      {/* Premium Calculator */}
      <PremiumCalculatorForm 
        onCalculate={handleCalculate} 
        insuranceType={type}
      />

      {/* Premium Result */}
      {calculatedPremium !== null && (
        <Card className="my-6">
          <CardHeader>
            <CardTitle>Perkiraan Premi Anda</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="text-3xl font-bold text-primary">
              {formatRupiah(calculatedPremium)}/bulan
            </div>
            <p className="text-sm text-muted-foreground">
              Ini adalah perkiraan premi berdasarkan input Anda. Premi aktual dapat berbeda berdasarkan faktor tambahan.
            </p>
            <div className="bg-muted/50 p-4 rounded-lg">
              <h4 className="font-semibold mb-2">Ringkasan Profil Anda:</h4>
              <div className="grid md:grid-cols-2 gap-2 text-sm">
                <div><span className="text-muted-foreground">Usia:</span> {formData.age} tahun</div>
                <div><span className="text-muted-foreground">Kode Pos:</span> {formData.zipCode}</div>
                <div><span className="text-muted-foreground">Pertanggungan:</span> {formatRupiah(formData.coverage)}</div>
              </div>
            </div>
          </CardContent>
        </Card>
      )}

      {/* Providers with Adjusted Pricing */}
      {calculatedPremium !== null && (
        <div className="space-y-6">
          <h2 className="text-2xl font-semibold">Pilih Penyedia Anda</h2>
          <p className="text-muted-foreground">
            Bandingkan penyedia dengan harga yang dipersonalisasi. Tinjau manfaat dan pertimbangan untuk membuat pilihan terbaik.
          </p>
          
          <div className="space-y-4">
            {data.map((item, index) => {
              const adjustedPrice = getAdjustedPrice(item.price, calculatedPremium);
              return (
                <Card key={index} className="hover:shadow-lg transition-shadow">
                  <CardHeader>
                    <div className="flex justify-between items-start">
                      <div>
                        <CardTitle className="text-xl">{item.provider}</CardTitle>
                        <Badge variant="secondary" className="mt-1">{item.rating}</Badge>
                      </div>
                      <div className="text-right">
                        <div className="text-2xl font-bold text-primary">
                          {formatRupiah(adjustedPrice)}/bulan
                        </div>
                        <div className="text-sm text-muted-foreground">
                          Asli: {item.price}
                        </div>
                      </div>
                    </div>
                  </CardHeader>
                  <CardContent className="grid md:grid-cols-2 gap-6">
                    <div>
                      <h4 className="font-semibold mb-2">Detail Pertanggungan</h4>
                      <div className="space-y-1 text-sm">
                        <div><span className="text-muted-foreground">Cakupan:</span> {item.coverage}</div>
                        <div><span className="text-muted-foreground">Deductible:</span> {item.deductible}</div>
                      </div>
                    </div>
                    <div>
                      <h4 className="font-semibold mb-2 text-green-600">Manfaat</h4>
                      <ul className="text-sm space-y-1">
                        {item.features.map((feature, idx) => (
                          <li key={idx}>• {feature}</li>
                        ))}
                      </ul>
                    </div>
                    <div>
                      <h4 className="font-semibold mb-2 text-destructive">Pertimbangan</h4>
                      <ul className="text-sm space-y-1">
                        {item.disadvantages.map((disadv, idx) => (
                          <li key={idx}>• {disadv}</li>
                        ))}
                      </ul>
                    </div>
                  </CardContent>
                  <CardContent>
                    <Button className="w-full" size="lg">
                      Pilih {item.provider}
                    </Button>
                  </CardContent>
                </Card>
              );
            })}
          </div>
        </div>
      )}

      {/* Navigation */}
      <div className="flex justify-between items-center">
        <Link href={`/insurance/${type}`}>
          <Button variant="outline">
            ← Kembali ke Perbandingan
          </Button>
        </Link>
        <Link href="/">
          <Button variant="ghost">
            Kembali ke Beranda
          </Button>
        </Link>
      </div>
    </div>
  );
}
