"use client";
import React, { useState } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

interface PremiumCalculatorFormProps {
  onCalculate: (premium: number, formData: any) => void;
  insuranceType: string;
}

export default function PremiumCalculatorForm({ onCalculate, insuranceType }: PremiumCalculatorFormProps) {
  const [age, setAge] = useState<number | "">("");
  const [zipCode, setZipCode] = useState("");
  const [coverage, setCoverage] = useState<number | "">("");
  const [error, setError] = useState("");

  const getInsuranceSpecificFields = () => {
    switch (insuranceType) {
      case 'auto':
        return (
          <>
            <div className="grid md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="vehicleYear">Tahun Kendaraan</Label>
                <Input 
                  id="vehicleYear" 
                  type="number" 
                  placeholder="2020" 
                  min="1990" 
                  max={new Date().getFullYear()}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="vehicleType">Jenis Kendaraan</Label>
                <select name="vehicleType" className="w-full h-10 px-3 border rounded-md bg-background">
                  <option value="Sedan">Sedan</option>
                  <option value="SUV">SUV</option>
                  <option value="MPV">MPV</option>
                  <option value="Truk">Truk</option>
                  <option value="Motor">Motor</option>
                </select>
              </div>
            </div>
            <div className="grid md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="drivingExperience">Tahun Memiliki SIM</Label>
                <Input 
                  id="drivingExperience" 
                  name="drivingExperience"
                  type="number" 
                  placeholder="5" 
                  min="0" 
                  max="80"
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="accidents">Kecelakaan (3 Tahun Terakhir)</Label>
                <Input 
                  id="accidents" 
                  name="accidents"
                  type="number" 
                  placeholder="0" 
                  min="0" 
                  max="10"
                />
              </div>
            </div>
          </>
        );
      case 'health':
        return (
          <>
            <div className="grid md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="smoker">Status Merokok</Label>
                <select name="smoker" className="w-full h-10 px-3 border rounded-md bg-background">
                  <option value="Non-smoker">Tidak Merokok</option>
                  <option value="Former smoker">Bekas Perokok</option>
                  <option value="Current smoker">Masih Merokok</option>
                </select>
              </div>
              <div className="space-y-2">
                <Label htmlFor="healthStatus">Status Kesehatan</Label>
                <select name="healthStatus" className="w-full h-10 px-3 border rounded-md bg-background">
                  <option value="Excellent">Sangat Baik</option>
                  <option value="Good">Baik</option>
                  <option value="Fair">Cukup</option>
                  <option value="Poor">Kurang</option>
                </select>
              </div>
            </div>
            <div className="space-y-2">
              <Label htmlFor="familySize">Jumlah Anggota Keluarga</Label>
              <Input 
                id="familySize" 
                name="familySize"
                type="number" 
                placeholder="1" 
                min="1" 
                max="10"
              />
            </div>
          </>
        );
      case 'life':
        return (
          <>
            <div className="grid md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="healthStatus">Status Kesehatan</Label>
                <select name="healthStatus" className="w-full h-10 px-3 border rounded-md bg-background">
                  <option value="Excellent">Sangat Baik</option>
                  <option value="Good">Baik</option>
                  <option value="Fair">Cukup</option>
                  <option value="Poor">Kurang</option>
                </select>
              </div>
              <div className="space-y-2">
                <Label htmlFor="smoker">Status Merokok</Label>
                <select name="smoker" className="w-full h-10 px-3 border rounded-md bg-background">
                  <option value="Non-smoker">Tidak Merokok</option>
                  <option value="Former smoker">Bekas Perokok</option>
                  <option value="Current smoker">Masih Merokok</option>
                </select>
              </div>
            </div>
            <div className="space-y-2">
              <Label htmlFor="coverageTerm">Masa Pertanggungan (Tahun)</Label>
              <select name="coverageTerm" className="w-full h-10 px-3 border rounded-md bg-background">
                <option value="10">10 Tahun</option>
                <option value="20">20 Tahun</option>
                <option value="30">30 Tahun</option>
                <option value="Whole Life">Seumur Hidup</option>
              </select>
            </div>
          </>
        );
      case 'home':
        return (
          <>
            <div className="grid md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="homeValue">Nilai Rumah (Rp)</Label>
                <Input 
                  id="homeValue" 
                  name="homeValue"
                  type="number" 
                  placeholder="500000000" 
                  min="100000000" 
                  max="10000000000"
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="homeAge">Usia Rumah (Tahun)</Label>
                <Input 
                  id="homeAge" 
                  name="homeAge"
                  type="number" 
                  placeholder="5" 
                  min="0" 
                  max="100"
                />
              </div>
            </div>
            <div className="grid md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="homeType">Tipe Rumah</Label>
                <select name="homeType" className="w-full h-10 px-3 border rounded-md bg-background">
                  <option value="Single Family">Rumah Tinggal</option>
                  <option value="Condo">Apartemen</option>
                  <option value="Townhouse">Rumah Susun</option>
                  <option value="Multi-Family">Rumah Komersial</option>
                </select>
              </div>
              <div className="space-y-2">
                <Label htmlFor="securitySystem">Sistem Keamanan</Label>
                <select name="securitySystem" className="w-full h-10 px-3 border rounded-md bg-background">
                  <option value="None">Tidak Ada</option>
                  <option value="Basic">Dasar</option>
                  <option value="Advanced">Lanjutan</option>
                  <option value="Professional Monitoring">Monitoring Profesional</option>
                </select>
              </div>
            </div>
          </>
        );
      default:
        return null;
    }
  };

  const calculatePremium = (e: React.FormEvent) => {
    e.preventDefault();

    // Basic validations
    if (!age || !zipCode || !coverage) {
      setError("Mohon isi semua field yang wajib.");
      return;
    }
    if (typeof age !== 'number' || age < 16 || age > 100) {
      setError("Mohon masukkan usia yang valid (16-100 tahun).");
      return;
    }
    if (typeof coverage !== 'number' || coverage < 1000000) {
      setError("Mohon masukkan jumlah pertanggungan yang valid (minimum Rp1.000.000).");
      return;
    }
    if (!/^\d{5}$/.test(zipCode)) {
      setError("Mohon masukkan kode pos 5 digit yang valid.");
      return;
    }

    setError("");

    // Calculate premium based on insurance type and inputs
    let basePremium = 0;
    const formData = new FormData(e.target as HTMLFormElement);
    const formValues = {
      age: Number(age),
      zipCode,
      coverage: Number(coverage),
      vehicleYear: Number(formData.get('vehicleYear')) || 2020,
      vehicleType: formData.get('vehicleType') || 'Sedan',
      drivingExperience: Number(formData.get('drivingExperience')) || 5,
      accidents: Number(formData.get('accidents')) || 0,
      smoker: formData.get('smoker') || 'Non-smoker',
      healthStatus: formData.get('healthStatus') || 'Good',
      familySize: Number(formData.get('familySize')) || 1,
      coverageTerm: formData.get('coverageTerm') || '20',
      homeValue: Number(formData.get('homeValue')) || 500000000,
      homeAge: Number(formData.get('homeAge')) || 5,
      homeType: formData.get('homeType') || 'Single Family',
      securitySystem: formData.get('securitySystem') || 'None'
    };

    switch (insuranceType) {
      case 'auto':
        basePremium = 800000 + (Number(coverage) / 10000000) * 50000;
        basePremium += Math.max(0, (25 - Number(age)) * 200000); // Young driver surcharge
        basePremium += Math.max(0, (formValues.accidents) * 2000000); // Accident surcharge
        break;
      case 'health':
        basePremium = 200000 + (Number(coverage) / 1000000) * 500;
        basePremium += Math.max(0, (Number(age) - 30) * 100000); // Age-based increase
        if (formValues.smoker === 'Current smoker') basePremium *= 1.5;
        break;
      case 'life':
        basePremium = 50000 + (Number(coverage) / 100000000) * 10000;
        basePremium += Math.max(0, (Number(age) - 25) * 2000); // Age-based increase
        if (formValues.smoker === 'Current smoker') basePremium *= 2;
        break;
      case 'home':
        basePremium = 600000 + (Number(coverage) / 10000000) * 25000;
        basePremium += Math.max(0, (formValues.homeAge) * 50000); // Age-based increase
        if (formValues.securitySystem === 'Professional Monitoring') basePremium *= 0.9;
        break;
    }

    onCalculate(Math.round(basePremium), formValues);
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Kalkulator Premi</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={calculatePremium} className="space-y-6">
          <div className="grid md:grid-cols-3 gap-4">
            <div className="space-y-2">
              <Label htmlFor="age">Usia *</Label>
              <Input 
                id="age" 
                type="number" 
                value={age} 
                onChange={(e) => setAge(e.target.value ? Number(e.target.value) : "")} 
                placeholder="25" 
                min="16" 
                max="100"
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="zipCode">Kode Pos *</Label>
              <Input 
                id="zipCode" 
                type="text" 
                value={zipCode} 
                onChange={(e) => setZipCode(e.target.value)} 
                placeholder="12345" 
                maxLength={5}
                pattern="\d{5}"
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="coverage">Jumlah Pertanggungan (Rp) *</Label>
              <Input 
                id="coverage" 
                type="number" 
                value={coverage} 
                onChange={(e) => setCoverage(e.target.value ? Number(e.target.value) : "")} 
                placeholder="50000000" 
                min="1000000"
                required
              />
            </div>
          </div>

          {getInsuranceSpecificFields()}

          {error && (
            <div className="text-sm text-destructive bg-destructive/10 p-3 rounded-md">
              {error}
            </div>
          )}
          
          <Button type="submit" className="w-full">
            Hitung Premi
          </Button>
        </form>
      </CardContent>
    </Card>
  );
}
